import React, { useState }from 'react';
import './App.css';
import WorldMap from 'react-world-map';
// import Menu from "./button.js"


 function Button(props) {
 	function handleClick() {
 		props.select_country(props.con);
 		props.get_news(props.con);
 	}

	return (
		<button className = 'button button2' onClick={handleClick}>
		{props.con}
		</button>
		);	
}

function Menu(props){
	// var con2 = [];
	// for (let i in props.json) {
	// 	if (i[1] == props.cont){
	// 		con2.push(i)
	// 		console.log(i)
	// 	}

	var con2= props.json.filter(con=>con[1]==props.cont.toUpperCase())
	
	return (
		<>
		{con2.map(con => <Button 
		con={con[0]} key = {props.json.indexOf(con)} 
		select_country = {props.select_country}
		get_news = {props.get_news}
		/>)}
		</>
	);

}


function Headline(props){
	return (
		<li class="list-group-item">{props.headline}</li>
		)	
}

function Listings(props){
	if (props.country == "") {
		return(
			<>
			</>
			)
	}
	else
	{
		return(
		<div >
		<h3 className = 'news'>{"Some news from " + props.country}</h3>
		<ul class="list-group list-group-flush">
		{props.news.map(headline=><Headline headline = {headline} key = {props.news.indexOf(headline)}/>)}
		</ul>
		</div>
		)
	}
}


function App(props) {
	const [country, setCountry] = useState("");
	const select_country = (c) =>{ 
		setCountry(c);
	};

	const [news, setNews] = useState([]);
	const get_news = (con) => {
		let j = [];
		var url = 'https://kushagrahans.me/listings/'+con;
		fetch(url)
		.then(response => response.json())
		.then((json)=>{
			for(var i = 0; i<json.length; i++){
			j.push(json[i]);
			}
		setNews(j);
		});
	};

	const [cont, setCont] = useState('as');
	const select_cont = (cont) => {
		setCont(cont);
	};

	return (
		<div>
			<h2 className= 'display-4 heading'>N   E   W   S</h2>
			<div class="row">
				<div className = 'col-xl map'>	
     				<WorldMap  selected={cont} onSelect={setCont} />
     			</div>
     			<div className = 'col-xl menu'>
     				<Menu cont = {cont} json = {props.json} select_country = {select_country} get_news = {get_news}/>
    			</div>
    		</div>
    		<div className="container listings">
    		
    					<Listings news = {news} country = {country}/>
    				
    		</div>				
    	</div>
      		
    	

  );
}


export default App;
